import React from 'react'
import Delete from '@mui/icons-material/Delete';
import { useCart,useDispatch } from '../Contextreducer';
export default function Cart() {
  let data = useCart();
  let dispatch = useDispatch();

  if (data.length === 0) {
    
      return(
      <div>
        <div className='m-5 w-100 text-center fs-3' style={{ color: 'white' }}>Cart is empty!</div>
      </div>
    )
  }
  // const handleRemove = (index)=>{
  //   console.log(index)
  //   dispatch({type:"REMOVE",index:index})
  // }

  const handleCheckOut = async () => {
    let userEmail = localStorage.getItem("userEmail");
  
    // console.log(data,localStorage.getItem("userEmail"),new Date())
    let response = await fetch("http://localhost:3000/api/orderData", {
      // credentials: 'include',
      // Origin:"http://localhost:3000/login",
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        order_data: data,
        Email: userEmail,
        order_date: new Date().toLocaleString('en-US', { 
          dateStyle: 'full',
          timeStyle: 'medium'
        })
      })
    })

    console.log("JSON RESPONSE:::::", response.status)
    if (response.status === 200) {
      let json = await response.json();
      if (json.success) {
        <div class="alert alert-success" role="alert">
        Order Placed successfully
      </div>
      }
      dispatch({ type: "DROP" })
    }
  }

  let totalPrice = data.reduce((total, food) => total + food.price, 0)
  return (
    <div>

      {console.log(data)}
      <div className='container m-auto mt-5 table-responsive  table-responsive-sm table-responsive-md' >
        <table className='table table-hover '>
          <thead className=' text-success fs-4'>
            <tr >
              <th scope='col' >#</th>
              <th scope='col' >Name</th>
              <th scope='col' >Quantity</th>
              <th scope='col' >Option</th>
              <th scope='col' >Amount</th>
              <th scope='col' ></th>
            </tr>
          </thead>
          <tbody>
            {data.map((food, index) => (
              <tr key={`${food.id}-${index}`}>
                <th scope='row' style={{ color: 'white' }}>{index + 1}</th>
                <td style={{ color: 'white' }}>{food.name}</td>
                <td style={{ color: 'white' }}>{food.qty}</td>
                <td style={{ color: 'white' }}>{food.size}</td>
                <td style={{ color: 'white' }}>{food.price}</td>

                <td ><button type="button" className="btn p-0"><Delete style={{ color: 'white' }} onClick={() => { dispatch({ type: "REMOVE", index: index }) }} /></button> </td></tr>
            ))}
          </tbody>
        </table>
        <div><h1 className='fs-2' style={{ color: 'white' }}>Total Price: {totalPrice}/-</h1></div>
        <div>
          <button className='btn bg-success mt-5 ' onClick={handleCheckOut} > Check Out </button>
        </div>
      </div>



    </div>
  )
}